import 'cross-fetch/polyfill';
import blobToBuffer from 'blob-to-buffer';
import fontkit from 'fontkit';

const unpackMetricsFromFont = font => {
  const {
    capHeight,
    ascent,
    descent,
    lineGap,
    unitsPerEm,
    familyName,
    xHeight
  } = font;
  return {
    familyName,
    capHeight,
    ascent,
    descent,
    lineGap,
    unitsPerEm,
    xHeight
  };
};
const fromFile = path =>
// @ts-expect-error Can remove when PR to update types is merged: https://github.com/DefinitelyTyped/DefinitelyTyped/pull/63204
fontkit.open(path).then(unpackMetricsFromFont);
const fromBlob = async blob => new Promise((resolve, reject) => {
  blobToBuffer(blob, (err, buffer) => {
    if (err) {
      return reject(err);
    }
    try {
      resolve(unpackMetricsFromFont(fontkit.create(buffer)));
    } catch (e) {
      reject(e);
    }
  });
});
const fromUrl = async url => {
  const response = await fetch(url);
  const blob = await response.blob();
  return fromBlob(blob);
};

export { fromBlob, fromFile, fromUrl };
